       i{\n                .close-base(-3px);\n            }\n        }\n\n        &-with-desc{\n            .@{notice-prefix-cls}-notice-close{\n                top: 11px;\n            }\n        }\n    }\n\n    &-title {\n        font-size: @font-size-base;\n        color: @title-color;\n        padding-right: 10px;\n        overflow: hidden;\n        text-overflow: ellipsis;\n        white-space: nowrap;\n    }\n    &-with-desc &-title{\n        font-weight: bold;\n        margin-bottom: 8px;\n    }\n    &-with-desc&-with-icon &-title{\n        margin-left: 51px;\n    }\n\n    &-desc {\n        font-size: 12px;\n        //color: @legend-color;\n        color: @text-color;\n        text-align: justify;\n        line-height: 1.5;\n    }\n    &-with-desc&-with-icon &-desc{\n        margin-left: 51px;\n    }\n\n    &-with-icon &-title{\n        margin-left: 26px;\n    }\n\n    &-icon {\n        position: absolute;\n        left: 20px;\n        margin-top: -1px;\n        font-size: 16px;\n\n        &-success {\n            color: @success-color;\n        }\n        &-info {\n            color: @primary-color;\n        }\n        &-warning {\n            color: @warning-color;\n        }\n        &-error {\n            color: @error-color;\n        }\n    }\n    &-with-desc &-icon{\n        font-size: 36px;\n    }\n\n    &-custom-content{\n        &:after{\n            content: \"\";\n            display: block;\n            width: 4px;\n            position: absolute;\n            top: 0;\n            bottom: 0;\n            left: 0;\n        }\n    }\n    &-with-normal{\n        &:after{\n            background: @primary-color;\n        }\n    }\n    &-with-info{\n        &:after{\n            background: @primary-color;\n        }\n    }\n    &-with-success{\n        &:after{\n            background: @success-color;\n        }\n    }\n    &-with-warning{\n        &:after{\n            background: @warning-color;\n        }\n    }\n    &-with-error{\n        &:after{\n            background: @error-color;\n        }\n    }\n}","@radio-prefix-cls: ~\"@{css-prefix}radio\";\n@radio-group-prefix-cls: ~\"@{radio-prefix-cls}-group\";\n@radio-inner-prefix-cls: ~\"@{radio-prefix-cls}-inner\";\n@radio-group-button-prefix-cls: ~\"@{radio-group-prefix-cls}-button\";\n\n.@{radio-group-prefix-cls} {\n    display: inline-block;\n    font-size: @font-size-small;\n    vertical-align: middle;\n    &-vertical{\n        .@{radio-prefix-cls}-wrapper {\n            display: block;\n            height: 30px;\n            line-height: 30px;\n        }\n    }\n}\n\n// 普通状态\n.@{radio-prefix-cls}-wrapper {\n    font-size: @font-size-small;\n    vertical-align: middle;\n    display: inline-block;\n    position: relative;\n    white-space: nowrap;\n    margin-right: 8px;\n    cursor: pointer;\n    &-disabled{\n        cursor: @cursor-disabled;\n    }\n}\n\n.@{radio-prefix-cls} {\n    display: inline-block;\n    margin-right: 4px;\n    white-space: nowrap;\n    outline: none;\n    position: relative;\n    line-height: 1;\n    vertical-align: middle;\n    cursor: pointer;\n    &:hover {\n        .@{radio-inner-prefix-cls} {\n            border-color: #bcbcbc;\n        }\n    }\n    &-inner {\n        display: inline-block;\n        width: 14px;\n        height: 14px;\n        position: relative;\n        top: 0;\n        left: 0;\n        background-color: #fff;\n        border: 1px solid @border-color-base;\n        border-radius: 50%;\n        transition: all @transition-time @ease-in-out;\n\n        &:after {\n            position: absolute;\n            width: 8px;\n            height: 8px;\n            left: 2px;\n            top: 2px;\n            border-radius: @border-radius-base;\n            display: table;\n            border-top: 0;\n            border-left: 0;\n            content: ' ';\n            background-color: @primary-color;\n            opacity: 0;\n            transition: all @transition-time @ease-in-out;\n            transform: scal